import { _decorator, Component, Node, instantiate, Vec3 } from "cc";
import { EasyLevels } from "../Levels/EasyLevels";
import { InGameAssets } from "../InGameAssets";
import { LevelData } from "../Levels/LevelData";
const { ccclass, property } = _decorator;

@ccclass("GridGenerator")
export class GridGenerator extends Component {
  @property(Node) dummyCandy: Node = null!;

  start() {
    this.setNodeActive(this.dummyCandy, true);
    this.testEffects();
  }

  update(deltaTime: number) {}

  private setNodeActive(settingNode: Node, isActive: boolean) {
    settingNode.active = isActive;
  }

  private async testEffects() {
    // Seviye verilerini al
    const levelData: LevelData = EasyLevels.levels[1];

    for (let row = 0; row < levelData.rows; row++) {
      for (let col = 0; col < levelData.cols; col++) {
        const currentGridCellData = levelData.grid[row][col];
        const assetType = InGameAssets.NodeMap.get(currentGridCellData);
        const asset = assetType[1]
        if (asset) {
          // Yeni node'u instantiate et
          const newNode = instantiate(asset);
          
          // Pozisyonunu ayarla
          const position = this.getTilePosition(row, col, 100); // 64 örnek tile boyutu
          newNode.setPosition(position);
          
          // Node'u sahneye ekle
          this.node.addChild(newNode);
          newNode.active = true;
        }
      }
    }
  }

  // Griddeki her bir tile'ın pozisyonunu hesaplar
  private getTilePosition(row: number, col: number, tileSize: number): Vec3 {
    const x = col * tileSize + tileSize / 2;
    const y = row * tileSize + tileSize / 2;
    return new Vec3(x, y, 0);
  }
}
